@use 'styles/colors';
@import 'styles/base';

:host {
  .page-block {
    @extend %page-block;
  }

  .navigation-block {
    background: #FFFFFF;
    box-shadow: 0 0 20px 5px rgba(0, 0, 0, 0.03);
    border-top: 1px solid rgba(237, 237, 237, 0.5);
    border-bottom: 1px solid rgba(237, 237, 237, 0.5);
  }

  .page-title {
    @extend %page-title;
  }

  .outlet {
    margin-top: 36px;
  }
}
